.container{
  text-align: center;
  padding: 40px;
  min-height: 100vh;
  background-color: aqua;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.button{
  margin: 20px 0;
}

.btn{
  padding: 10px 20px;
  margin: 8px;
  border: none;
  color: aliceblue;
  cursor: pointer;
}
.happy{
  background-color: rgb(210, 210, 30);

}
.sad{
  background-color: rgb(224, 117, 45);
}

.excited{
  background-color: rgb(85, 117, 177);
}

.angry{
  background-color: rgb(118, 49, 158);
}

.box{
  margin-top: 30px;
  font-size: 24px;
}